#editorcontainer {
    overflow: hidden;
}

.toolbar {
    background: none;
    border: none;
}

.timeslider-bar {
    background: none;
}

#editbar {
    background: $base;
    border-bottom: 1px solid #ccc;
}

/** iframe #ace_outer **/
#outerdocbody {
    overflow: hidden;
    background: none;
}

/** iframe #ace_inner **/
#innerdocbody, #padcontent {
    background: none;
    * {
        font-family: 'Noto Serif', serif;
        font-size: 16px;
        font-weight: normal;
        line-height: 175%;
    }

    h1, h2, h3, h4, h5, h6 {
        margin: 20px 0;
    }

    h1 *, h2 *, h3 *, h4 *, h5 *, h6 * {
        font-family: 'Open Sans', sans-serif;
        color: $medium_black;
        font-weight: bold;
        line-height: 135%;
    }

    h1 * {
        font-size: 46px;
    }

    h2 * {
        font-size: 40px;
    }

    h3 * {
        font-size: 34px;
    }

    h4 * {
        font-size: 28px;
    }

    h5 * {
        font-size: 22px;
    }

    h6 * {
        font-size: 16px;
    }

    .image {
        img {
            width: auto;
            max-width: 100%;
        }
    }
    /** MOBILE **/
    @media (max-width: 535px) {
        h1 * {
            font-size: 30px;
        }

        h2 * {
            font-size: 24px;
        }

        h3 * {
            font-size: 18px;
        }

        h4 *, h5 *, h6 * {
            font-size: 16px;
        }
    }

    a {
        color: $deep_sky_blue;
    }

    b, b * {
        font-weight: bold;
    }

    code * {
        font-family: Consolas, Menlo, Monaco, 'Lucida Console', 'Liberation Mono', 'DejaVu Sans Mono', 'Bitstream Vera Sans Mono', 'Courier New', monospace, sans-serif
    }

    fs8 *, .fs8 * {
        font-size:8px
    }
    fs9 *, .fs9 * {
        font-size:9px
    }
    fs10 *, .fs10 * {
        font-size:10px;
    }
    fs11 *, .fs11 * {
        font-size:11px;
    }
    fs12 *, .fs12 * {
        font-size:12px;
    }
    fs13 *, .fs13 * {
        font-size:13px;
    }
    fs14 *, .fs14 * {
        font-size:14px;
    }
    fs15 *, .fs15 * {
        font-size:15px;
    }
    fs16 *, .fs16 * {
        font-size:16px;
    }
    fs17 *, .fs17 * {
        font-size:17px;
    }
    fs18 *, .fs18 * {
        font-size:18px;
    }
    fs19 *, .fs19 * {
        font-size:19px;
    }
    fs20 *, .fs20 * {
        font-size:20px;
    }

    [data-color="black"] *, .color\:black * {
        color:black;
    }
    [data-color="red"] *, .color\:red * {
        color:red;
    }
    [data-color="green"] *, .color\:green * {
        color:green;
    }
    [data-color="blue"] *, .color\:blue * {
        color:blue;
    }
    [data-color="yellow"] *, .color\:yellow * {
        color:yellow;
    }
    [data-color="orange"] *, .color\:orange * {
        color:orange;
    }

}

#epAuthorshipToggleAuthorList {
    font-family: bold, sans-serif;
    background: $base;
}

#padpage {
    #timeslider-top {
        background: $base;

        * {
            font-family: bold, sans-serif;
        }

        .toolbar * {
            font-family: regular, sans-serif;
        }

        #steppers .stepper {
            font-family: fontawesome-etherpad;
        }
    }
}

/** Chat flickers on load, don't want that **/
#chatbox, #chaticon {
    display: none !important;
}

.popup, #users, #connectivity {
    background: $base;
    border-top: 1px solid $base;
    border-right: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    border-left: 1px solid #ccc;
    border-radius: 0;
    z-index: 1500 !important;
    margin-top: -1px;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
}

#inline_toolbar {
    width: max-content;
    height: auto;
    right: 0;
    margin-top: 31px;

    .menu_inline {
        margin-left: 15px;
        display: inline-block;
        position: sticky;
        width: auto;
        height: auto;
        background-color: $medium_black;
        
        ul {
            position: relative;
            margin: 5px;

            li {
                height: auto;
            
                &.link {
                    width: auto;
                    color: $white;
                    font-family: "Open Sans";
                    font-size: 13px;
                    line-height: 18px;
            
                    a {
                        background: none;
                        box-shadow: none;
                        border: none;
                    }
                    .buttonicon {
                        width: auto;
                        height: auto;
                        font-family: "Open Sans";
                        color: $white;

                        &:hover {
                            opacity: 0.7;
                        }
                    }
                }
            }
        }
    }

    .menu_inline:after {
        content: ' ';
        position: absolute;
        width: 0;
        height: 0;
        left: -15px;
        right: auto;
        top: 0px;
        bottom: auto;
        border: 15px solid;
        border-color: $medium_black transparent transparent transparent;
    }
}
.comment-icon {
    height: 24px!important;
    width: 24px!important;
    content: ' '!important;
    background: #FFC600;

    .comment-icon-counter {
        height: 18px;
        width: 100%;
        color: #252525;
        font-family: "Open Sans";
        font-size: 13px;
        font-weight: bold;
        line-height: 24px;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }
}
.comment-icon:before {
    display: none;
}

.comment-icon:after {
    content: ' ';
    position: relative;
    height: 0;
    right: auto;
    bottom: auto;
    width: 0;
    left: 3px;
    top: -5px;
    border: 8px solid;
    border-color: transparent #FFC600 transparent transparent;
    z-index: -1;
}

.comment-modal {
    width: -moz-calc(100% - 40px) !important;
    width: -webkit-calc(100% - 40px) !important;
    width: -o-calc(100% - 40px) !important;
    width: calc(100% - 40px) !important;
    left: 0 !important;
    max-width: 100% !important;
    border: none !important;
    padding: 0!important;
    margin-left: 10px!important;
    margin-right: 10px!important;
    .comment-reply-input-label {
        display:block;
    }

    #comments {
        display: block!important;
        width: -moz-calc(100% - 40px);
        width: -webkit-calc(100% - 40px);
        width: -o-calc(100% - 40px);
        width: calc(100% - 40px);
    }
    .reply-comment-suggest {
        .reply-suggestion-checkbox:after {
            content: ' ';
            height: 20px;	width: 20px;
            display: inline-block;
            border: 1px solid #E5E5E5;
            border-radius: 4px;
            background-color: $white !important;
            margin: 0 20px 0 0;
            vertical-align:middle;
        }
    }
    .reply-suggestion {
        &.active {
            display: block;
            margin-top: 20px;

            p {
                width: initial;
                color: #252525;
                font-family: "Open Sans";
                font-size: 16px;
                font-weight: bold;
                line-height: 20px;
            }
            .reply-comment-suggest-to, .reply-comment-changeFrom-value {
                display: block;
                height: auto;
                width: -moz-calc(100% - 20px);
                width: -webkit-calc(100% - 20px);
                width: -o-calc(100% - 20px);
                width: calc(100% - 20px);
                border: 1px solid #E5E5E5;
                border-radius: 4px;
                background-color: #FFFFFF;
                font-family: "Open Sans";
                font-size: 14px;
                line-height: 20px;
                margin-top: 12px;
                margin-bottom: 16px;
                padding: 6px 10px;
                font-weight: normal;
            }
            input {
                display: block!important;
            }
        }
        
    }
    input[type="submit"] {
        height: 30px;
        width: 149px;
        border: 1px solid #0574E3;
        border-radius: 4px;
        background-color: #0680FC;
        color: #FFFFFF;
        font-family: "Open Sans";
        font-size: 14px;
        font-weight: bold;
        line-height: 20px;
        text-align: center;
        margin: 20px 0;
    }
    .comment-reply {
        note {
            display: block!important;
            height: max-content;
            width: 100% !important;
            margin-left: -20px !important;
            padding: 20px !important;
            
            .comment-reply-text {
                display: block;
                clear: both;
                width: 100%;
                color: #252525;
                font-family: "Open Sans";
                font-size: 14px;
                line-height: 20px;
                margin-top: 2px;
                margin-bottom: 11px;
                font-weight: normal!important;
            }

            .comment-changeTo-form {
                p, .comment-changeFrom, .comment-changeTo {
                    span.comment-changeFrom-label, span.comment-changeTo-label, .reply-comment-suggest-from-p{
                        height: 20px;
                        width: initial;
                        color: #252525;
                        font-family: "Open Sans";
                        font-size: 16px;
                        font-weight: bold;
                        line-height: 20px;
                        margin: 0 20px 0 0;
                        padding: 0;
                        clear: both;
                    }
    
                    .comment-changeFrom-value, .comment-changeTo-value, .comment-changeTo-value {
                        display: block;
                        height: auto;
                        width: -moz-calc(100% - 20px);
                        width: -webkit-calc(100% - 20px);
                        width: -o-calc(100% - 20px);
                        width: calc(100% - 20px);
                        border: 1px solid #E5E5E5;
                        border-radius: 4px;
                        background-color: #FFFFFF;
                        font-family: "Open Sans";
                        font-size: 14px;
                        line-height: 20px;
                        margin-top: 12px;
                        margin-bottom: 16px;
