.comment-modal:after {
    content: ' ';
    position: absolute;
    width: 0;
    height: 0;
    left: 0px;
    right: auto;
    top: -15px;
    bottom: auto;
    border: 15px solid;
    border-color: transparent transparent transparent $white;
